*{
    margin:0;
    padding:0;
    text-decoration: none;
    --white: #ffffff;
    --green1: #00ff00;
    --green2: #18c305;
    --green3: #169000;
    --green4: #0d5503;
    --black: #000000 ;
    --greenLowO:rgba(13, 85, 3, 0.055);
    font-family: 'round-digitalio';
    font-style: normal;
    letter-spacing: 0.2rem;
    src: local('round-digitalio'), url('./assets/font/round-digitalio.ttf') format('truetype');
}

@font-face {
    font-family: 'round-digitalio';
    font-style: normal;
   
    src: local('round-digitalio'), url('./assets/font/round-digitalio.ttf') format('truetype');
  }

li {
    list-style-type:none;
}

.liCustom {
    font-size: calc(0.8vw + 0.7vh + 0.5vmin);
    backdrop-filter: blur(5px);
    padding-bottom: 2rem;
    border-bottom: 1px solid green;
    color: #00ff00;
    background-color: rgba(13, 85, 3, 0.8);
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    width: auto;
}
.ubuntu-regular {
    font-family: "Ubuntu", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  .ubuntu-bold {
    font-family: "Ubuntu", sans-serif;
    font-weight: 700;
    font-style: normal;
  }
  
  .ubuntu-regular-italic {
    font-family: "Ubuntu", sans-serif;
    font-weight: 400;
    font-style: italic;
  }
  
  .ubuntu-bold-italic {
    font-family: "Ubuntu", sans-serif;
    font-weight: 700;
    font-style: italic;
  }




body {
    background: white;


}

.container{
    display:flex;
    overflow-y: hidden;
}
main{
    width: 97%;
  
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    margin-top: 80px;
}
.sidebar{
    z-index: 100;
    background: linear-gradient(-45deg, #00ff00fd, #18c305, #169000, #ffffff);
    border-bottom: 2px solid var(--green1);
	background-size: 400% 400%;
	animation: gradient 25s ease infinite;
    color: #ffffff;
    height: 75px;
    width: 100%;
    max-width: 1350px;
    transition: all 0.5s;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(1rem, 1fr)) ;
    gap: 0.5rem;
    align-items: center;
    justify-content: center;
    align-content: center;
    align-self: center;
    backdrop-filter: blur(4px);
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;

}
.top_section{
    display: flex;
    display: flex;
    flex-direction: row;
    align-items:center;
    padding:20px 15px;
    align-items: center;
    max-width: 70px;
}
.logo{
    height: 100%;
    
    transition: all 300ms;
    width: 50px;
}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}

.logo:hover {
    transform: rotateY(360deg);
}


.link{
    display: flex;
    color: var(--black);
    padding: 10px ;
    max-width: 180px;
    transition: all 0.3s;
    align-self: center;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
}
.link:hover{
    background:var(--black);
    color: #ffffff;
    transition: all 0.3s;
    border-radius: 0.5rem;
    font-weight: 700;
    border-bottom: 2px solid var(--green1);
}
.active{
    background: lightskyblue;
    color: #000;
    align-self: center;
    align-items: center;
    justify-content: center;
}
.icon, .link_text{
    font-size:1rem;
}


section {
    width: 100%;
    height: 75vh;
    margin-top: 1em;
    margin-bottom: 0.5em;
 
    border-radius: 0.5rem;

}

.section2 {
    width: 100%;
    height: 75vh;
    margin-top: 1em;
    margin-bottom: 0.5em;
    background-color: #0d5503fb;
    border-radius: 0.5rem;
    z-index: 50;
}

.section3 {
    width: 100%;
    height: 75vh;
    margin-top: 1em;
    margin-bottom: 0.5em;
    background-color: rgb(230, 230, 230);
    border-radius: 0.5rem;
    z-index: 50;
}

.NavigationButtons {
    display: flex;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    align-items: center;
    align-content: center;
   
}

.bbbfallinganimation {
    width: 100vw;
    height: 60vh;
    position: absolute;
}

.HomeSection {
    background: none;
    display: grid;
    width: 98%;
    align-items: center;
    align-self: center;
    padding: 1rem;
   
}
.mainTitle {
    font-size: calc(5vw + 5.5vh + 3.5vmin);
    align-content: center;
    align-items: center;
    align-self: center;
    width: 100%;
    
}

.centeredMainText {
    align-items: center;
    max-width: 1350px;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    align-content: center;
    align-self: center;
    position: absolute;
    top: 0;
}

.MainTitleContainer {
    color: var(--green4);
    
    width: 100%;
   margin-right: auto;
   margin-left: auto;

    padding: 1rem;
    padding-right: 3rem;
    position: relative;
    z-index: 100;
    
    border-radius: 1rem;
  
    height: 50vh;
    justify-content: center;
    align-items: flex-start;
    align-self: center;
    display: flex;
    
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.MainContainer {
    overflow: hidden;
    display: flex;
   justify-content: flex-start;
   flex-direction: column;
    align-items: center;
    align-self: center;
    max-width: 1350px;
    margin-left: auto;
    margin-right: auto;
    height: 100%;
    width: 100%;
   
}

.BobbyContainer {
 top: 10%;
    width: 50%;
    position: absolute;
    
    justify-content: center;
    align-items: center;
    align-self: center;
    display: flex;
   
}

.parallaxIMG1 {
    
    position: relative;
    top: 3rem;
max-width: 100%;
    width: auto;
    height: 100%;
    z-index: 1;
}
.parallaxIMG2 {
    max-width: 100%;
    width: auto;
    height: 100%;
    z-index: 0;
    top: 5rem;
    position: relative;
}

.bobbyCentered {
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    justify-content: center;
    align-items: center;
    align-self: center;
    display: flex;
    width: auto;
    z-index: 1;
}

.sidebarM {
    display: none;
    
}
@media screen and (max-width: 900px) {
    .BobbyContainer {
        width: 90%;
    }
}


@media screen and (max-width: 700px) and (orientation: portrait) {
    .sidebar {
        display: none;
    }
    .sidebarM {
    display: flex;
    flex-direction: column;
    z-index: 100;
    background: linear-gradient(-45deg, #00ff00fd, #18c305, #169000, #ffffff);
    border-bottom: 2px solid var(--green1);
	background-size: 400% 400%;
	animation: gradient 25s ease infinite;
    color: #ffffff;
    height:80vh;
    width: 100px;
    
    transition: all 0.5s;
    position: fixed;
    top: 0;
    left: 0;

    
    
 
    align-items: center;
    justify-content: space-evenly;
    align-content: center;
    align-self: center;
    backdrop-filter: blur(4px);
   
    border-bottom-right-radius: 0.5rem;
    }
}

@media screen and (max-width: 500px) {
    .BobbyContainer {
        width: 120%;
        top: 25%;
    }
}